html {
  height: 100%;
  margin: 0;
}

body {
  background: url(/ReactPortfolio2019/static/media/Background.204e1e32.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
  margin: 0;
}

.container {
  margin-top: 10%;
}

.myShadow {
  text-shadow: 2px 2px 8px #61b3ff;
}

.myShadow span {
  text-shadow: 2px 2px 8px rgb(255, 255, 255);
}

.myBackground {
  background-color: #07274a;
  opacity: 0.7;
}

#loading {
  height: 40px;
}
#loading {
  -webkit-animation: rotation 2s infinite linear;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
  }
}

@media only screen and (max-width: 600px) {
  body {
    background: url(/ReactPortfolio2019/static/media/Background.204e1e32.jpg);
    background-repeat: no-repeat;
    background-size: 100rem;
    height: auto;
    margin: 0;
  }

  .container {
    padding-top: 10%;
  }

  .aboutContainer {
    padding-bottom: 10%;
  }
}

